import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Card, Header } from "semantic-ui-react";
import moment from "moment";

export default class EventCard extends Component {
  render(){
    const { event } = this.props
    return (
      <Card fluid>
        <Card.Content>
          <Link to={`/${event.id}`}>
            <Card.Header as="h3">
              {event.title}
            </Card.Header>
          </Link>
          <Card.Meta>
            {`${moment(event.start).format("h:mm-")}${moment(event.end).format("h:mm")} ${moment(event.start).format("D MMMM")}`}
          </Card.Meta>
        <Card.Description>
          {event.description}
        </Card.Description>
        </Card.Content>
      </Card>
    )
  }
}
